



























































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import comhead from "../../components/Comhead.vue";
import { Toast,Dialog } from "vant";

@Component({
  components: {
    Swipe,
    comhead
  },
  mixins: [mixin]
})
export default class Cart extends Vue {
  @Action("setPrepareId") setPrepareId;

  @Action("setTabIndex") setTabIndex;
  result = [];
  checked = false;
  cartList = [];
  totalMoney = 0.0; //总金额
  maxHeightdiv() {
    return (
      "height:" +
      (this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availHeight -
        152) +
      "px;"
    );
  }
  handleImageWidth1() {
    return (
      "width:" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth *
        0.35+
      "px;height:" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth *
        0.35 +
      "px;"
    );
  }
  allSelect() {
    if (this.checked) {
      let result = [];
      this.cartList.forEach((item, index) => {
        console.log();
        result.push(item.id);
      });
      this.result = result;
    } else if (this.cartList.length == this.result.length) {
      this.result = [];
    }
  }
  totalPrice() {
    var totalMoney = 0;
    for (var i = 0; i < this.cartList.length; i++) {
      if (this.result.indexOf(this.cartList[i].id) !== -1) {
        totalMoney = totalMoney + this.cartList[i].price.toFixed(2) * this.cartList[i].num.toFixed(2);
      }
    }
    totalMoney = totalMoney * 100;
    totalMoney.toString();
    return totalMoney;
  }
  checkchange() {
    if (this.result.length == this.cartList.length) {
      this.checked = true;
    } else {
      this.checked = false;
    }
  }
  onSubmit() {
    if (this.result.length <= 0) {
      Toast("您还没选择商品");
      return;
    }

    Vue.prototype.$reqFormPost(
      "/prepare/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartIdList: this.result.join(",")
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        console.log(res);

        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.setPrepareId(res.data.data.prepareId);
        this.$router.push({
          path: "/settle"
        });
        console.log("预支付订单ID", res.data.prepareId);
      }
    );
  }
  deleteCart(index, collect) {
    Dialog.confirm({
        title: "提示",
        message: "您确定要将这些商品从购物车中移除吗?"
    })
      .then(() => {
        Vue.prototype.$reqFormPost(
              "/shop/cart/delete",
              {
                userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
                  .userId,
                token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
                  .token,
                cartIds: this.cartList[index].id
              },
              res => {
                if (res == null) {
                  console.log("网络请求错误！");
                  return;
                }
                if (res.data.status != 200) {
                  console.log(
                    "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
                  );
                  Toast(res.data.message);
                  return;
                }
                if (!collect) {
                  Toast("已删除");
                }
                this.getCartList();
              }
            );

     })
      .catch(() => {
        // on cancel
      });
  }
  //删除购物车商品
  deleteshopCart() {
    if (this.result.length <= 0) {
      Toast("您还没选择商品");
      return;
    }
    Dialog.confirm({
        title: "提示",
        message: "您确定要将这些商品从购物车中移除吗?"
    })
     .then(() => {
        Vue.prototype.$reqFormPost(
          "/shop/cart/delete",
          {
            userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
              .userId,
            token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
              .token,
            cartIds: this.result.join(",")
          },
          res => {
            if (res == null) {
              console.log("网络请求错误！");
              return;
            }
            if (res.data.status != 200) {
              console.log(
                "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
              );
              Toast(res.data.message);
              return;
            }
            Toast("已删除");
            this.getCartList();
          }
        );
        }).catch(() => {
            // on cancel
          });
      }
      collect(index) {
        Vue.prototype.$reqFormPost(
          "/fav/add",
          {
            userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
              .userId,
            token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
              .token,
            goodsId: this.cartList[index].goodsId
          },
          res => {
            if (res == null) {
              console.log("网络请求错误！");
              return;
            }
            if (res.data.status != 200) {
              console.log(
                "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
              );
              Toast(res.data.message);
              return;
            }
            this.deleteCart(index, true);
            Toast("已移至收藏夹");
          }
        );
  }

  getCartList() {
    Vue.prototype.$reqFormPost(
      "/shop/cart/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
              if (res.data.status != 200 && (res.data.message ||'') !=='') {
          Toast(res.data.message);
          return;
        }
        console.log("查询购物车", res.data);

        this.cartList = res.data.data.carts;
        var div = document.getElementById("cartLen");
        if (!document.getElementById("cartLen")) {
          div = document.createElement("div");
          div.setAttribute("id", "cartLen");
          div.className = "messageFexid";
          div.style.right = "11px";
          var diva = document.getElementsByClassName(
            "van-tabbar-item__text"
          )[2];
          diva.appendChild(div);
        }
        if (res.data.data.carts.length > 0) {
          div.innerHTML = this.getNumber(this.cartList);
        } else {
          div.style.display = "none";
        }
      }
    );
  }
   changeNum(id, num){
     if(!num || num == 0){
return ;
     }
setTimeout(()=>{
Vue.prototype.$reqFormPost(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId: id,
        num: num
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("加1", res.data);
        this.getCartList();
      }
    );
},500)
 }
  pluscart(id, num) {
    Vue.prototype.$reqFormPost(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId: id,
        num: num
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("加1", res.data);
        this.getCartList();
      }
    );
  }
  isLogin() {
    return (
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO] &&
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].userId !==
        "" &&
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].token !==
        ""
    );
  }
  minuscart(id, num) {
    Vue.prototype.$reqFormPost(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId: id,
        num: num
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("减1", res.data);
        this.getCartList();
      }
    );
  }

  goindex() {
    this.$router.push("/");
  }
  getNumber(cartList = []) {
    let num = 0;
    cartList.forEach((item, index) => {
      num += item.num;
    });
    return num.toString();
  }
  mounted() {
    this.setTabIndex(2);

    if (
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].userId !=
        "" &&
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].token != ""
    ) {
      this.getCartList();
    }
  }
}



